import React from "react";
import {Link} from "react-router-dom";


const Header = (props) => {
    return (
        <header className="p-3 bg-dark text-white">
            <div className="container">
                <div className="d-flex flex-wrap align-items-center justify-content-center justify-content-lg-start">
                    <Link to='/' className="d-flex align-items-center mb-2 mb-lg-0 text-white text-decoration-none">
                        <img src="img/logo.svg" alt="" className="logo-header"/>
                    </Link>

                    <ul className="nav col-12 col-lg-auto me-lg-auto mb-2 justify-content-center mb-md-0">
                        <li><Link to='/' className="nav-link px-2 text-secondary">Главная</Link></li>
                        <li><Link to='/projects' className="nav-link px-2 text-white">Проекты</Link></li>
                        <li><Link to='/todo' className="nav-link px-2 text-white">Заметки</Link></li>
                    </ul>

                    {/*<form className="col-12 col-lg-auto mb-3 mb-lg-0 me-lg-3">*/}
                    {/*    <input type="search" className="form-control form-control-dark" placeholder="Search..."*/}
                    {/*           aria-label="Search"/>*/}
                    {/*</form>*/}

                    <div className="text-end">
                        {props.isAuthenticated() ?
                            <div>
                                <Link to='/logout' onClick={() => props.logout()} type="button"
                                      className="btn btn-outline-light me-2">Выйти</Link>

                                <span>Привет, {props.current_user()}! </span>
                            </div>
                            :
                            <Link to='/login' className="btn btn-outline-light me-2">Войти</Link>
                        }
                        {props.isAuthenticated() ?
                            ''
                            :
                            <button type="button" className="btn btn-warning">Sign-up</button>

                        }


                    </div>
                </div>
            </div>
        </header>
    )
}

export default Header;